import { Category } from "@/types/types";

type Props = {
  switchWorks: (category: Category | null) => void;
};
export const CategoryNav = ({ switchWorks }: Props) => {
  const categories: Category[] = [
    "design",
    "cording",
    "wordpress",
    "direction",
    "react/Next.js",
  ];

  return (
    <nav>
      <button onClick={() => switchWorks(null)}>all</button>
      {categories.map((cat) => (
        <button onClick={() => switchWorks(cat)} key={cat}>
          {cat}
        </button>
      ))}
    </nav>
  );
};
